.header {
  display: flex;
  flex-direction: column;
}

.header-buttons {
  display: none;
}

.header-buttons > * {
  text-transform: uppercase;
}

.header-logo {
  display: flex;
  flex-direction: column;
}

.header-logo > * {
  text-align: center;
  text-transform: uppercase;
}

.header-basket {
  display: flex;
  flex-wrap: wrap;
}

.header-basket__icon {
  flex-basis: 50%;
}

.header-basket__checkout {
  flex-basis: 50%;
  text-transform: uppercase;
}

.header-basket__search {
  display: none;
}

@media (min-width: 768px) {
  .header {
    flex-direction: row;
    justify-content: space-between;
  }

  .header-buttons {
    display: flex;
    flex-direction: column;
  }

  .header-basket__search {
    display: block;
    flex-basis: 100%;
  }
}